
.star-rating__checkbox {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
}
.star-rating__star {
    display: inline-block;
    padding: 4px;
    vertical-align: middle;
    line-height: 1;
    font-size: 4em;
    color: #ABABAB;
    transition: color .8s ease;
}
@media (max-width: 640px) {
.star-rating__star {
        font-size: 2.8em;
}
}
.star-rating__star:hover {
        cursor: pointer;
}
.star-rating__star.is-selected {
        color: gold;
}
.star-rating__star.is-disabled:hover {
        cursor: default;
}



